.coffee__slider {
	min-width: 0;
	position: relative;
}

// .slider__content {
// 	max-width: 480px;
// }

.coffee__slider .slick-arrow {
	cursor: pointer;
	position: absolute;
	top: 50%;
	z-index: 10;
	font-size: 0;
	width: 24px;
	height: 24px;
	border-width: 1px;
	border-color: $borderDark;
	border-style: solid;
	border-radius: 100px;
	padding: 15px;
}

.coffee__slider .slick-arrow.slick-prev {
	left: 0;
	background: url(/src/images/arrow-left.svg) 0 0 no-repeat;
	@media(max-width: 380px) {
		opacity: 0;
	}
}

.coffee__slider .slick-arrow.slick-next {
	right: 0;
	background: url(/src/images/arrow-right.svg) 0 0 no-repeat;
	@media(max-width: 380px) {
		opacity: 0;
	}
}

// .coffee__slider .slick-prev,
// .coffee__slider .slick-next {
//   display: none; /* or opacity: 0; */
// }

.slick-track {
	display: flex;
}

.slick-list {
	overflow: hidden;
}

.coffee__slider .slick-dots{
	padding-top: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 12px;
}
.coffee__slider .slick-dots li{
	list-style: none;
}
// /* Активная точка */
.coffee__slider .slick-dots li.slick-active button{
	background-color: $borderDark;
}
/* Элемент точки */
.coffee__slider .slick-dots button {
	font-size: 0;
	width: 40px;
	height: 4px;
	background-color: $borderLight;
}