.header {
	padding-top: 20px;
}

.header__wrapper {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}

.menu {
	@media(max-width: 768px) {
		&__icon {
			display: block;
			width: 44px;
			height: 44px;
			position: relative;
			z-index: 5;
			cursor: pointer;
			transition: .2s all ease-in-out;
			border-width: 1px;
			border-radius: 100px;
			border-color: $borderDark;
			border-style: solid;
			// span {
			// 	position: absolute;
			// 	top: calc(50% - 1px);
			// 	left: 0;
			// 	width: 100%;
			// 	width: 16px;
			// 	height: 2px;
			// 	background-color: $textDark;
			// 	&:first-child {
			// 		top: 0;
			// 	}
			// 	&:last-child {
			// 		top: auto;
			// 		bottom: 0;
					
			// 	}
			// }
		}
	}
	&__body {
		@media (max-width: 768px) {
			position: absolute;
			top: 0;
			left: -100%;
			height: 100vh;
			width: 100%;
			display: flex;
			transition: all .2s;
			overflow: auto;
		}
		&.active {
			left: 0;
		}
	}
	&__list {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		gap: 40px;
		@media (max-width: 768px) {
			flex-direction: column;
			gap: 0;
			margin: auto;
		}
	}
	&__link {
		font-size: 16px;
		font-weight: 600;
		line-height: 150%; /* 24px */
		color: $textDark;
		@media (max-width: 768px) {
			display: block;
			font-size: 32px;
			line-height: 90px;
		}
	}
}

.menu__icon-line {
	width: 16px;
	height: 1px;
	background-color: #333;
	position: absolute;
	transition: transform 0.3s ease-in-out;
}

.menu__icon-line:first-child  {
	top: 40%;
	left: 30%;
}

.menu__icon-line:last-child  {
	bottom: 40%;
	left: 30%;
}

.assortm__link {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 8px;
	@media(max-width: 768px) {
		display: none;
	}
}

.assortm__title {
	font-size: 16px;
	font-weight: 600;
	line-height: 150%;
	color: $textDark;
}

section {
	padding-top: 100px;
}

.title {
	// text-align: center;
	font-size: 60px;
	font-weight: 600;
	line-height: 125%;
	@media(max-width: 380px) {
		font-size: 32px;
	}
}
